// Polyfills for things that don't need to block the rest of the JS

( function( $ ) {
	$( document ).ready( () => {
		// CSS :focus-visible
		$.getScript( 'https://unpkg.com/focus-visible' );

		// CSS :focus-within
		if ( ! Modernizr.focuswithin ) {
			$.getScript( 'https://unpkg.com/focus-within-polyfill' );
		}
	} );
} )( jQuery );
